/* reset css */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
  font-family: "Karla", sans-serif;
  font-size: 1.6rem;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Custom Additions Below */
:root, * {
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}

body {
  width: 100%;
  height: 100vh;
  /* header style */
}
body header {
  background-color: #fafafa;
}
body header .container {
  padding-top: 3rem;
}
body header .container nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
body header .container nav img {
  margin-left: 2.5rem;
  display: inline-block;
  width: 11.1rem;
  height: 2rem;
}
body header .container nav img, body header .container nav button {
  margin-bottom: 2.5rem;
}
body header .container nav button {
  margin-right: 2.5rem;
  display: inline-block;
  width: 3rem;
  height: 3rem;
  color: #837d87;
  outline: 0.2rem solid #837d87;
}
body header .container nav button#times {
  display: none;
}
body header .container nav ul {
  position: absolute;
  top: 12%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  padding: 5.4rem 2.4rem 29.9rem 2.4rem;
  width: 100%;
  background-image: url(/images/bg-pattern-mobile-nav.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position-y: bottom;
  background-color: #2b272f;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #fafafa;
  display: none;
}
body header .container nav ul.show {
  display: flex;
}
body header .container nav ul li:last-of-type {
  padding: 1.6rem 10rem;
  border: 0.1rem solid #fafafa;
  white-space: nowrap;
}

@media screen and (min-width: 1440px) {
  body {
    /* header style */
  }
  body header .container {
    padding-top: 1.8rem;
  }
  body header .container nav img {
    margin: 1rem 0 0 16.5rem;
  }
  body header .container nav button {
    display: none;
  }
  body header .container nav ul {
    display: block;
    all: unset;
    display: flex;
    gap: 2rem;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 0.2rem;
    text-transform: uppercase;
    color: #837d87;
    list-style: none;
  }
  body header .container nav ul li:last-of-type {
    margin-right: 16rem;
    padding: 1.25rem 2.5rem;
    background-color: #2d2640;
    color: #fafafa;
  }
  body header .container nav ul li:not(:last-of-type) {
    margin-top: 1.3rem;
  }
  body header .container nav ul li:not(:last-of-type):hover {
    color: #2b272f;
  }
}